import React from 'react'
import './Nabvar.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faBars} from '@fortawesome/free-solid-svg-icons'
import {Link} from 'react-router-dom'
import Logo from '../../assets/Logo.png'


const Navbar = () => {
    return (
        <nav className="navbar fixed-top   navbar-expand-lg navbar-light bg-light   ">
            <Link className="navbar-brand" to="/aboutMe"><img className="nav-logo" src={Logo} alt="Tabish Khan"></img></Link>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
               <FontAwesomeIcon icon={faBars} style={{color:'white'}}/>
            </button>

            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav ml-auto">
                    {/* <li className="nav-item active">
                        <Link className="routeLink" to="/aboutMe">AboutMe</Link><span className="sr-only">(current)</span>
                    </li> */}
                    <li className="nav-item">
                        <Link className="routeLink" to="/myProjects">MyProjects</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="routeLink" to="/photoBucket">Photo Bucket</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="routeLink" to="/contactMe">Contact Me</Link>
                    </li>
                    {/* <li className="nav-item">
            <Link className="routeLink" to="/contactMe">ContactMe</Link>
                    </li> */}

                    

                </ul>

            </div>
        </nav>
    )
}

export default Navbar
