import React from 'react'
import './Projects.css'
import Mosip from '../../assets/mosip.png'
import Cart from '../../assets/Cart.PNG'
import Spring from '../../assets/Spring.PNG'

const Projects = () => {
    return (
        <div className="myProjects">
           <div className="row">
                <div className="col-md-4">
                    <div className="card text-center">
                        <img className="card-image-mosip" src={Mosip} alt="image here"></img>
                        <div className="card-body">
                                <h5 className="card-title">MOSIP</h5>
                                <p className="card-text">MOSIP is a modular and open source identity platform that helps user organisations such as Governments implement a digital, foundational ID in a cost effective way, while embracing the best practices of scalability, security and privacy harnessing the power of open source.</p>
                                <a href="https://github.com/mosip" target="_blank">SOURCE CODE</a>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className="card text-center">
                        <img className="card-image-cart" src={Cart} alt="image here"></img>
                        <div className="card-body">
                                <h5 className="card-title">Create-My-Cart</h5>
                                <p className="card-text-cart">A simple google chrome extension using javaScript with which a user can add products from different ecom websites to his/her custom cart.Just add it to your extension library and enable it.
                                The extension should be downloaded and then the user needs to install it into chrome.</p>
                                <a className="cart_link" href="https://github.com/tabishkhan7/create-my-cart-extension" target="_blank">SOURCE CODE</a>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className="card text-center">
                        <img className="card-image-spring" src={Spring} alt="image here"></img>
                        <div className="card-body">
                                <h5 className="card-title">Spring_Jwt_Rolebased_Otp_Auth</h5>
                                <p className="card-text">A spring boot project with role based auth for apis and authenticating the user wen he provides the otp which was sent to him/her over a mail provided by him/her.The project is based on the micro-services architecture and can be included as local dependency or as a code snippet.</p>
                                <a href="https://github.com/tabishkhan7/spring_jwt_rolebased_otp_authentication" target="_blank">SOURCE CODE</a>
                        </div>
                    </div>
                </div>
           </div>
        </div>
    )
}

export default Projects
