import React from 'react'
import AboutMeStyle from './AboutMe.css'
import {Link} from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {  faFacebookF,faLinkedin,faGithub,faTwitter  } from '@fortawesome/free-brands-svg-icons'

const AboutMe = () => {
    return (
        <div className="AboutMe">
            <div className="main-info">
                
            <div className="main-intro">
            <p>Hi! I am <span style={{color:'#07d86f'}}>Tabish Khan.</span></p>
            <p>Welcome To My World!!</p></div>
                {/* <Typed className="typed-text"
                    strings={["JAVA", "NODE.JS", "ANGULAR", "REACT", "UI5"]}
                    typeSpeed={40}
                    backSpeed={60}
                    loop></Typed> */}
                <Link className="hoverButton" to="/skills"><span>Know More</span></Link>
            </div>
                        <div className="contactMe">
                           <a href="https://www.facebook.com/champ.tabish" target="_blank"><FontAwesomeIcon className="brand-icon" icon={faFacebookF} /></a>
                           <a href="https://www.linkedin.com/in/tabish-khan-546705106/" target="_blank"><FontAwesomeIcon className="brand-icon" icon={faLinkedin} /></a>
                           <a href="https://github.com/tabishkhan7" target="_blank"><FontAwesomeIcon className="brand-icon" icon={faGithub} /></a>
                           <a href="https://twitter.com/TabishK77" target="_blank"><FontAwesomeIcon className="brand-icon" icon={faTwitter} /></a>
                        </div>
        </div>

    )
}

export default AboutMe
