import React, { useEffect } from 'react'
import './MySkills.css'
import Aos from 'aos'
import 'aos/dist/aos.css'
const MySkills = () => {

    useEffect(()=>{
        Aos.init({duration:2000});
    },[])
    return (
        <div className="main">
            <div className="main-content">
                <div data-aos="fade-up" className="about-content">
                    <p style={{ color: "white" }}>
                        Professionally connected to the software industry for close to 3 years.<br></br>
                        Currently working as a software developer in <span>SAP Labs.</span><br></br>
                        Well organised,problem solver with a willingness to always take up new challenges and working towards a better tomorrow.<br></br>
                        Loves to travel.Fan of football.<br></br>
                        Intrested to work as a full stack developer on ambitous project and people.<br></br>
                        Have a look at some of my memorable days in my journey so far.
                    </p>
                    <div data-aos="fade-right" className="timeline-container">
                        <div className="timeline">
                            <ul>
                                <li>
                                    <div className="timeline-content">
                                        <h3 className="date">20th May, 2018</h3>
                                        <h1>Graduated </h1>
                                        <p>Completed my B.Tech and got an offer during the placement drives in college, thereby marking my entry to the opportunity filled software indusry.    </p>
                                    </div>
                                </li>
                                <li>
                                    <div className="timeline-content">
                                        <h3 className="date">30th June, 2018</h3>
                                        <h1>Joined Mindtree Ltd</h1>
                                        <p>Joined Mindtree as a software developer. Learned technologies like JAVA,Angular 6,React.js, Bootstrap, Html, CSS, Jenkins etc in their training program. Later was part of an open source identity platform team as a Full Stack Java Developer.</p>
                                    </div>
                                </li>
                                <li>
                                    <div className="timeline-content">
                                        <h3 className="date">17th Jan, 2020</h3>
                                        <h1>Earned my first awards</h1>
                                        <p>After successfully delivering the project to the client,was recognized as on of the STAR PERFORMER OF THE PROJECT. Also earned several otther accolades from the peers.</p>
                                    </div>
                                </li>
                                <li>
                                    <div className="timeline-content">
                                        <h3 className="date">22nd Feb, 2021</h3>
                                        <h1>Joined SAP Labs</h1>
                                        <p>After completing nearly 3 successfull years at Mindtree, joined SAP Labs as a software developer. Currently learning technologies like Node.js,ABAP etc.</p>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>

                <div data-aos="flip-left" className="main-skills">
                <h2>My Skills</h2>
                    <li>
                        <h5>JAVA</h5>
                        <span className="bar"><span className="JAVA"></span></span>
                    </li>
                    <li>
                        <h5>Node.js</h5>
                        <span className="bar"><span className="Node"></span></span>
                    </li>
                    <li>
                        <h5>Angular</h5>
                        <span className="bar"><span className="Angular"></span></span>
                    </li>
                    <li>
                        <h5>React.js</h5>
                        <span className="bar"><span className="React"></span></span>
                    </li>
                    <li>
                        <h5>Html/Css</h5>
                        <span className="bar"><span className="HtmlCss"></span></span>
                    </li>
                </div>
            </div>

        </div>
    )
}

export default MySkills
