import './ContactMe.css'
import firebase from '../../assets/firebase'
import 'firebase/firestore';
import React, { useEffect, useState } from 'react'
import profilePic from '../../assets/profile_pic.jpeg'
import Aos from 'aos'
import 'aos/dist/aos.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faMapMarker,faPhone,faEnvelope} from '@fortawesome/free-solid-svg-icons'

const ContactMe = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [number, setNumber] = useState("");
  const [nameErr, setNameErr] = useState("");
  const [emailErr, setEmailErr] = useState("");
  const [numberErr, setNumberErr] = useState("");
  const [loader, setLoader] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    const isValid = formValidated();
    if (isValid) {
      setLoader(true);
      const db = firebase.firestore()
      db.collection("contacts")
        .add({
          name: name,
          email: email,
          message: message,
          phoneNumber: number
        })
        .then(() => {
          setLoader(false);
          alert("Your message has been submitted👍");
        })
        .catch((error) => {
          alert(error.message);
          setLoader(false);
        });

      setName("");
      setEmail("");
      setMessage("");
      setNumber("");
    } else {
      alert("Please check your enteries")
    }
  };

  const formValidated = () => {
    const nameErr = {};
    const numErr = {};
    const emailErr = {};
    let isValid = true;
    if (name.trim().length < 4) {
      nameErr.nameErrMsg = "Name is too short";
      isValid = false;
    }
    if (!/\S+@\S+\.\S+/.test(email)) {
      emailErr.emailErrMsg = "Please enter a valid Email";
      isValid = false;
    }
    if (/^\+\d{1,3}-\d{9,10}$/.test(number)) {
      numErr.numErrMessage = "Phone Number Invalid";
      isValid = false;
    }
    setNameErr(nameErr);
    setEmailErr(emailErr);
    setNumberErr(numErr);
    return isValid;
  }
  useEffect(() => {
    Aos.init({ duration: 2000 });
  }, [])

  return (
    <div className="contact-main">
      <div className="personal-info">
        <div data-aos="fade-up" className="personal-card">
          <div className="personal-val">
          <FontAwesomeIcon className="profile-icon" icon={faMapMarker} />
          <p>Lucknow,Uttar Pradesh,India</p   >
          </div>
          <div className="personal-val-phone">
          <FontAwesomeIcon className="profile-icon" icon={faPhone} />
          <p>+91-6393853250</p>
          </div>
          <div className="personal-val-mail">
          <FontAwesomeIcon className="profile-icon" icon={faEnvelope} />
          <p>tabish.khansfc@gmail.com</p>
          </div>
        </div>
      </div>



      <div data-aos="flip-left" className="contact-section">
        <form className="contact-form" onSubmit={handleSubmit}>
          <h1>Send Me A Message</h1>
          <div className="border"></div>

          <input
            className="contact-form-text"
            placeholder="Name"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />

          <input
            className="contact-form-text"
            placeholder="Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <input
            className="contact-form-text"
            placeholder="Number"
            value={number}
            onChange={(e) => setNumber(e.target.value)}
          />
          <textarea
            className="contact-form-text"
            placeholder="Message"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
          ></textarea>

          <input
            type="submit"
            className="contact-form-btn"
          >

          </input>
        </form>
      </div>
    </div>
  )
}

export default ContactMe
