import React, { useEffect, useState } from 'react'
import firebase from '../../assets/firebase'
import 'firebase/firestore';
import './PersonalBlog.css'



const PersonalBlog = () => {
    const [image, setImages] = useState([]);
    
    useEffect(() => {
        const fetchUsers = async () => {
            const db = firebase.firestore()
            const imageSet = []
            const data = await db.collection("images").get().then(im => {
                im.docs.forEach(doc => {
                    const dat = doc.data();
                    imageSet.push(dat);

                })

            });

            setImages(imageSet);
        }
        fetchUsers()
    }, []);
    return ( 
                <div className="main">
                    <div className="gallery">
                            {image.map(img => (
                               <div className="img" key={img.name}> <img className="currentImage" key={img.name} src={img.image}></img> </div>
                            ))}
                    </div>
                </div>
           


        
    )
}

export default PersonalBlog
